@import '../../styles/breakpoints';

// SB Tab Variables:
// --sb-tab-height
// --sb-tab-color-text
// --sb-tab-color-border
// --sb-tab-color-background
// --sb-tab-active-color-border
// --sb-tab-active-color-background
// --sb-tab-disabled-color-text

.sb-tab {
  align-items: center;
  background-color: var(--sb-tab-color-background);
  border-color: var(--sb-tab-color-border, transparent);
  border-radius: var(--sbt-border-radius-small) var(--sbt-border-radius-small) 0
    0;
  border-style: solid;
  border-width: 1px 1px 3px;
  color: var(--sb-tab-color-text, var(--sbt-theme-tertiary-color-40));
  cursor: pointer;
  display: flex;
  font-family: var(--sbt-font-stack-primary);
  font-size: var(--sbt-font-size-30);
  font-weight: var(--sb-tab-font-weight, var(--sbt-font-weight-70));
  height: var(--sb-tab-height, 2.3125rem);
  justify-content: center;
  line-height: var(--sbt-line-height-25);
  padding: 1.125rem 1.25rem;
  position: relative;
  text-decoration: none;
  top: 1px;
  z-index: var(--sbt-stack-floating);

  @include window-width-max-small {
    font-size: var(--sbt-font-size-25);
    padding: 1.125rem 0.75rem;
  }

  &:hover {
    border-bottom-color: var(--sbt-theme-color-30);
  }
}

.sb-tab_active {
  --sb-tab-color-text: var(--sbt-theme-color-60);
  border-bottom-color: var(--sb-tab-color-text, var(--sbt-theme-color-60));

  &:hover {
    --sb-tab-color-text: var(--sbt-theme-color-60);
  }
}

.sb-tab_disabled {
  --sb-tab-color-text: var(
    --sb-tab-disabled-color-text,
    var(--sbt-theme-tertiary-color-20)
  );
  font-style: italic;
  pointer-events: none;
}
